html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
  background: #393939;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

#canvas-screenshot,
#img-screenshot,
#canvas-filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#canvas-screenshot ~ *,
#img-screenshot ~ *,
#canvas-filter ~ * {
  display: none;
}

.github-corner {
  fill: #fff;
  color: #222;
  position: absolute;
  top: 0;
  border: 0;
  right: 0;
  transition: all 0.2s ease-in-out;
  opacity: 0.85;
}

.github-corner:hover {
  opacity: 1;
}

@keyframes octocat-wave {
  0%, 100% {
    transform: rotate(0);
  }
  20%, 60% {
    transform: rotate(-25deg);
  }
  40%, 80% {
    transform: rotate(10deg);
  }
}

@media (min-width: 1024px) {
  body {
    overflow: hidden;
  }
  .main {
    display: flex;
    justify-content: space-between;
    width: 100vw;
    height: 100vh;
  }
  .github-corner:hover .octo-arm {
    animation: octocat-wave 560ms ease-in-out;
  }
}

.blocks-section {
  position: relative;
  padding: 1rem;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.blocks-section.ready {
  opacity: 1;
}

.blocks-section::before {
  content: "";
  padding-top: 50%;
  display: block;
}

.blocks-layout {
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
}

.blocks-layout::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #000;
  z-index: -1;
}

.blocks-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  grid-auto-flow: dense;
  grid-auto-rows: 80px;
  grid-gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  background: #000;
  transition: grid-gap 0.5s ease-in-out;
}

.blocks-grid li {
  position: relative;
  display: block;
  transition: all 0.5s ease-in-out;
}

.blocks-grid li::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("./../assets/bg-pattern.png");
  opacity: 0.075;
}

@media (min-width: 1024px) {
  .blocks-section {
    padding: 1rem;
    width: 80vw;
    flex: 0 0 auto;
    height: 100%;
  }
}

.settings-section {
  position: relative;
  background: #222;
  padding: 1rem;
  color: #fff;
  height: 100%;
  flex: 1 1 auto;
  overflow-y: auto;
  transition: all 0.2s ease-in-out;
}

.settings-top {
  text-align: center;
  margin: 0 0 1rem;
}

.settings-top img {
  width: 50px;
}

.settings-form {
  display: none;
}

.settings-form.active {
  display: block;
}

.settings-form fieldset {
  border: none;
  margin: 0 0 30px;
  padding: 0;
}

.settings-form fieldset legend {
  display: block;
  text-align: left !important;
  color: #fff;
  margin: 0 0 14px;
  text-align: center;
  font-size: 1.4rem;
  line-height: 1;
}

.settings-form fieldset > *:last-child {
  margin-bottom: 0 !important;
}

.settings-form table {
  width: 100%;
}

.settings-form table tr:last-child td {
  border-bottom: none;
}

.settings-form table td {
  vertical-align: middle;
  line-height: 1;
  border-bottom: 10px solid transparent;
}

.settings-form table td.align-top {
  vertical-align: top;
}

.settings-form table td:first-child {
  padding-right: 5px;
}

.settings-form label,
.settings-form .field-label {
  white-space: nowrap;
}

.settings-form input[type="text"],
.settings-form input[type="number"],
.settings-form input[type="range"] {
  width: 100%;
  max-width: 100%;
  border: 1px solid #4e4e4e;
  background: #393939;
  color: #fff;
  height: 40px;
  padding: 0 10px;
  margin: 0;
  transition: all 0.2s ease-in-out;
}

.settings-form input[type="text"]:hover, .settings-form input[type="text"]:focus,
.settings-form input[type="number"]:hover,
.settings-form input[type="number"]:focus,
.settings-form input[type="range"]:hover,
.settings-form input[type="range"]:focus {
  border-color: #fff;
}

.settings-form input[type="number"] {
  margin: 0;
  -moz-appearance: textfield;
  appearance: textfield;
}

.settings-form input[type="number"]::-webkit-inner-spin-button, .settings-form input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.settings-form input[type="range"] {
  background: none;
  border: none;
}

.settings-form button {
  display: block;
  width: 100%;
  background: #222;
  color: #fff;
  border: 1px solid #adadad;
  padding: 0;
  margin: 0 0 10px;
  text-transform: uppercase;
  text-align: center;
  height: 40px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.settings-form button.primary {
  background: forestgreen;
  border-color: forestgreen;
}

.settings-form button.primary:hover, .settings-form button.primary:focus {
  background: #28a328;
  border-color: #fff;
}

.settings-form button:hover, .settings-form button:focus {
  border-color: #fff;
}

.settings-form button.alert {
  background: firebrick;
  border-color: firebrick;
}

.settings-form button.alert:hover, .settings-form button.alert:focus {
  background: #e2322a;
  border-color: #fff;
}

.settings-form button:hover, .settings-form button:focus {
  border-color: #fff;
}

.field {
  position: relative;
  margin: 0 0 10px;
}

.field button {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  display: inline-block;
  border: none;
  background: none;
  padding: 0;
  margin: 0;
  font-size: 0;
  width: 20px;
  height: 20px;
  opacity: 0.75;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.field button:hover, .field button:focus {
  opacity: 1;
}

.field img {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.field-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.field-group > * {
  flex: 1 1 0px;
  margin: 0 10px 0 0;
}

.field-group > *:last-child {
  margin: 0;
}

.color-indicator {
  position: absolute;
  width: 15px;
  height: 15px;
  top: 50%;
  right: 40px;
  transform: translateY(-50%);
  border: 1px solid #4e4e4e;
  font-size: 0;
  text-indent: -9999px;
}

.divider {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  text-align: inherit;
  margin: 0 0 15px;
  border: 0;
  text-align: center;
}

.divider::after {
  content: "";
  display: inline-block;
  width: 100px;
  max-width: 100%;
  border-top: 1px solid #4e4e4e;
  vertical-align: top;
}

#draggable {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block !important;
}

.nub {
  position: absolute;
  width: 1.5rem;
  height: 1.5rem;
  background: #fff;
  border: 4px solid #5dc8cc;
  border-radius: 50%;
  touch-action: none;
  user-select: none;
}

.nub::before {
  content: "";
  position: absolute;
  width: .5rem;
  height: .5rem;
  background: #a8dadc;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100%;
  cursor: pointer;
}

.nub:active {
  border-color: #e2322a;
}

.nub:hover {
  border-width: 5px;
}
